<script>
import { supabase } from "../supabase";
export default {
  data() {
    return {
      url: "",
      urlErrorMessage: "",
      signInErrorMessage: "",
      showButton: true,
      email: "",
      password: "",
      loadingAuth: false,
      loged: false,
      username: "",
    };
  },
  async created() {
    console.log(location.href);
    const {
      data: { user },
    } = await supabase.auth.getUser();
    if (user) {
      this.loged = true;
      var emailParts = user.email.split("@");
      this.username = emailParts[0];
    }
  },
  methods: {
    async logWithGoogle() {
      this.loadingAuth = true;
      try {
        await supabase.auth.signInWithOAuth(
          {
            provider: "google",
            options: {
              redirectTo: location.href + "profile-page",
            },
          },

          (error, data) => {
            if (error) {
              // Handle the error
              console.error("Error signing in with Google:", error.message);
              this.loadingAuth = false;
            } else {
              console.log("Successfully signed in with Google");
              console.log("User data:", data);
              this.loadingAuth = false;
              this.$router.push({ name: "profilePage" });
            }
          }
        );
      } catch (error) {
        console.error("Error signing in with Google:", error.message);
        this.loadingAuth = false;
      }
    },
    async loginOrSignUp() {
      this.loadingAuth = true;
      try {
        const { data, error } = await supabase.auth.signUp({
          email: this.email,
          password: this.password,
        });

        if (error) {
          if (error.message.includes("already registered")) {
            // If the error indicates that the user already exists, try logging in
            const { data, error } = await supabase.auth.signInWithPassword({
              email: this.email,
              password: this.password,
            });

            if (error) {
              this.loadingAuth = false;
              this.signInErrorMessage = error.message;
              return;
            } else {
              console.log(data.session.user.id);
              this.loadingAuth = false;
              this.$router.push({ name: "profilePage" });
            }
          } else {
            this.loadingAuth = false;
            this.signInErrorMessage = error.message;
            return;
          }
        } else {
          this.loadingAuth = false;
          console.log(data.session.user.id);
          this.$router.push({ name: "profilePage" });
        }
      } catch (error) {
        this.loadingAuth = false;
        console.error("Error signing up or logging in:", error.message);
        this.signInErrorMessage = error.message;
        return;
      }
    },

    async sendUrl() {
      if (!this.isValidUrl(this.url)) {
        // Handle invalid URL
        this.urlErrorMessage = "Invalid URL";
        console.log("Invalid URL");
        return;
      }
      if (!this.url) {
        return;
      }
      this.$router.push({ name: "demoForm", params: { url: this.url } });
    },
    isValidUrl(url) {
      const pattern = new RegExp(
        "^(https?:\\/\\/)" +
          // protocol
          "((([a-zA-Z\\d]([a-zA-Z\\d-]{0,61}[a-zA-Z\\d])?)\\.)+[a-zA-Z]{2,6}" +
          // domain name
          "|(\\d{1,3}\\.){3}\\d{1,3}" +
          // OR ip (v4) address
          "|localhost)" +
          // OR localhost
          "(\\:\\d+)?(\\/[-a-zA-Z\\d%_.~+]*)*" +
          // port and path
          "(\\?[;&a-zA-Z\\d%_.~+=-]*)?" +
          // query string
          "(\\#[-a-zA-Z\\d_]*)?$"
        // fragment locator
      );
      return !!pattern.test(url);
    },

    async logout() {
      const { error } = await supabase.auth.signOut()
      console.error(error);
      this.username = "";
      this.loged = false;
    }
  },
};
</script>
