  </div>

  <div
    class="w-screen h-screen flex justify-center items-center"
    v-if="loadingBuild"
  >
    <span
      class="loading loading-spinner loading-lg text-primary opacity-100"
    ></span>
    building final app
  </div>

  <div
    class="w-screen h-screen absolute top-0 flex justify-center items-center z-30 bg-black bg-opacity-40"
    v-if="loadingData"
  >
    <span
      class="loading loading-spinner loading-lg text-primary opacity-100"
    ></span>
  </div>
</template>

<script>
import QrcodeVue from "qrcode.vue";
import axios from "axios";
import { supabase } from "@/supabase";
export default {
  data() {
    return {
      token: this.$route.params.token, // Replace with your actual token
      os: this.$route.params.os,
      downloadPageUrl: `http://34.125.138.233/downloadPageMobile/${this.$route.params.token}/${this.$route.params.os}`,
      appName: "",
      websiteUrl: "",
      logoSrc: "",
      packageName: "",
      email: "",
      loadingData: true,
      loadingBuild: false,
      watingForDownload: false,
      password: "",
      signInErrorMessage: "",
      loadingAuth: false,
    };
  },

  async created() {
    const token = this.$route.params.token;
    const os = this.$route.params.os;

    if (os == "android") {
      try {
        const response = await axios.get(
          `http://34.125.138.233/get-data-android/${token}`
        );
        this.appName = response.data.name;
        this.websiteUrl = response.data.url;
        this.packageName = response.data.packageName;
        this.logoSrc = "data:image/png;base64," + response.data.logo;
        this.loadingData = false;
      } catch (error) {
        console.error("Error fetching data:", error);
        this.loadingData = false;
        if (error.response.status === 404) {
          this.$router.push({ name: "notFound" });
        }
      }
    } else if (os == "ios") {
      try {
        const response = await axios.get(
          `http://34.125.138.233/get-data-ios/${token}`
        );
        this.appName = response.data.name;
        this.websiteUrl = response.data.url;
        this.packageName = response.data.packageName;
        this.logoSrc = "data:image/png;base64," + response.data.logo;
        this.loadingData = false;
      } catch (error) {
        console.error("Error fetching data:", error);
        this.loadingData = false;
        if (error.response.status === 404) {
          this.$router.push({ name: "notFound" });
        }
      }
    }
  },

  components: {
    QrcodeVue,
  },

  methods: {
    async logWithGoogle() {
      this.loadingAuth = true;
      try {
        const { user, session, error } = await supabase.auth.signInWithOAuth({
          provider: "google",
          options: {
            redirectTo: location.href,
          },
        });

        if (error) {
          console.error("Error signing in with Google:", error.message);
          this.loadingAuth = false;
        } else {
          console.log("Successfully signed in with Google");
          console.log("User data:", user, session);

          // Redirect to the profile page
          this.$router.push({
            name: "profilePage",
            params: { email: user.email },
          });
        }
      } catch (error) {
        console.error("Error signing in with Google:", error.message);
        this.loadingAuth = false;
      }
    },

    async handleSignUpOrLogin() {
      this.loadingAuth = false;
      try {
        const { data, error } = await supabase.auth.signUp({
          email: this.email,
          password: this.password,
        });

        if (error) {
          if (error.message.includes("already registered")) {
            // If the error indicates that the user already exists, try logging in
            const { data, error } = await supabase.auth.signInWithPassword({
              email: this.email,
              password: this.password,
            });

            if (error) {
              this.loadingAuth = false;
              this.signInErrorMessage = error.message;
              return;
            } else {
              console.log(data.session.user.id);
              this.loadingAuth = false;
              this.loadingBuild = true;
              this.buildFinalApp(); // call the build final app method
            }
          } else {
            this.loadingAuth = false;
            this.signInErrorMessage = error.message;
            return;
          }
        } else {
          this.loadingAuth = false;
          this.loadingBuild = true;
          console.log(data.session.user.id);
        }
      } catch (error) {
        this.loadingAuth = false;
        console.error("Error signing up or logging in:", error.message);
        this.signInErrorMessage = error.message;
        return;
      }
    },
    async downloadIosApp() {
      try {
        const response = await axios.get(
          `http://34.125.138.233/download-demo-zip-ios?token=${this.token}.0`,
          {
            responseType: "blob", // Set the response type to 'blob'
          }
        );

        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", "iosApp.zip"); // Set the desired file name
        document.body.appendChild(link);
        link.click();
      } catch (error) {
        console.error("Error downloading iOS app:", error);
        // Handle error as needed
      }
    },

    async downloadApk() {
      this.watingForDownload = true;
      try {
        const response = await axios.get(
          `http://34.125.138.233/download-demo-apk-android?token=${this.token}.0`,
          {
            responseType: "blob", // Set the response type to 'blob'
          }
        );
        this.watingForDownload = false;
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement("a");
        link.href = url;
        link.setAttribute("download", "apk-debu.apk"); // Set the desired file name
        document.body.appendChild(link);
        link.click();
      } catch (error) {
        this.watingForDownload = false;
        console.error("Error downloading apk", error);
        // Handle error as needed
      }
    },

    async buildFinalApp() {
      if (!this.loadingData) {
        try {
          const {
            data: { user },
          } = await supabase.auth.getUser();
          if (user) {
            console.log(user);
            this.loadingBuild = true;
            const formData = new FormData();
            formData.append("token", this.token);
            formData.append("email", user.email);

            const response = await axios.post(
              "http://34.125.138.233/build-final-android",
              formData,
              {
                headers: {
                  "Content-Type": "multipart/form-data",
                },
              }
            );
            console.log("Response:", response.data);
            this.loadingBuild = false;

            this.$router.push({
              name: "profilePage",
              params: {
                email: this.email,
              },
            });
          } else {
            const modal = document.getElementById("my_modal_2");

            modal.showModal();
          }
        } catch (error) {
          if (error.response.status === 404) {
            this.loadingBuild = false;
            this.$router.push({ name: "notFound" });
          }
        }
      }
    },

    editButton() {
      if (!this.loadingData) {
        this.$router.push({
          name: "edit",
          params: {
            token: this.token,
            status: "demo",
          
          },
        });
      }
    },
  },
};
</script>

